export default {
	data() {
		return {
			login: false,
			avatarUrl: '/static/logo.png',
			userInfo: {},
			loginTit:'登录'
		}
	},
	onLoad(){
		var userInfo = uni.getStorageSync("userInfo");
		if(userInfo){
			var userInfo = JSON.parse(userInfo);
			this.login = true;
			this.userInfo = userInfo
		}else{
			this.login = false;
		}
	},
	methods: {
		goLogin(){
			if (!this.login) {
				uni.navigateTo({
					url:'../login/login'
				})
			}else{
				uni.setStorageSync("userInfo","");
				this.onLoad();
			}
		},
		goContract(){
			uni.navigateTo({
				url:"../contract/contract"
			})
		},
		store(){
			uni.navigateTo({
				url:"../store/store"
			})
		},
		message(){
			uni.navigateTo({
				url:"../message/message"
			})
		},
		upload(){
			uni.navigateTo({
				url:"../uploadInfo/uploadInfo"
			})
		},
		invite(){
			uni.navigateTo({
				url:"../invite/invite"
			})
		},
		aboutUs(){
			uni.navigateTo({
				url:"../aboutUs/aboutUs"
			})
		}
		
	}
}
